html,
body,
#root {
  height: 100%; }

body {
  background-color: #F5F5F5;
  font-family: 'Muli', sans-serif; }
  body #root {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch; }
    body #root header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      height: 50px;
      padding: 0 1.5625em; }
    body #root main {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      flex: 1;
      min-height: 12.5em; }
    body #root footer {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 14px;
      font-size: 0.875em;
      height: 50px;
      width: 100%; }
